import { Injectable } from '@angular/core';
import { Employee } from 'src/models/employee.model';

@Injectable({
  providedIn: 'root'
})
export class EmployeeService {
  private _TAG = 'employee';
  private employeeInfo: Employee;
  constructor() {
    const storedUser = localStorage.getItem(this._TAG);
    if (storedUser) {
      this.employeeInfo = JSON.parse(storedUser);
    }
  }

  getName(): string {
    return this.employeeInfo.userName;
  }

  setEmployee(employee: Employee) {
    this.employeeInfo = employee;
    localStorage.setItem(this._TAG, JSON.stringify(employee));
  }

  getEmployee() {
    return this.employeeInfo;
  }

  checkEmployee() {

  }

  isEmployeeLogged(): boolean {
    if (this.employeeInfo) {
      return true;
    }
    return false;
  }

  logOutEmployee() {
    localStorage.setItem('token', null);
    localStorage.setItem('employee', null);
    this.employeeInfo = null;
  }
}
